import React from 'react';
import ReactDOM from 'react-dom';
import ReactFullpage from '@fullpage/react-fullpage';
import './index.css';

const FullpageComponent = () => {
  const openPdf = (pdfName) => {
    window.open(process.env.PUBLIC_URL + '/' + pdfName, '_blank');
  };

  const openLink = (url) => {
    window.open(url, '_blank');
  };

  return (
    <ReactFullpage
      scrollOverflow={require('fullpage.js').scrollOverflow}
      sectionsColor={["#282c34", "#282c34", "#282c34", "#282c34","#282c34"]} 
      render={({ state, fullpageApi }) => {
        return (
          <ReactFullpage.Wrapper>
            <div className="section">
              <img src={process.env.PUBLIC_URL + '/profile.png'} alt="Profile" />
            </div>
            <div className="section">
  <img src={process.env.PUBLIC_URL + '/title.png'} alt="Title" />
            <div className="button-container-center">
              <img 
                src={process.env.PUBLIC_URL + '/b0.png'} 
                alt="Button 0" 
                onClick={() => fullpageApi.moveTo(1)}
              />
              <img 
                src={process.env.PUBLIC_URL + '/b1.png'} 
                alt="Button 1" 
                onClick={() => fullpageApi.moveTo(3)}
              />
              <img 
                src={process.env.PUBLIC_URL + '/b2.png'} 
                alt="Button 2" 
                onClick={() => fullpageApi.moveTo(6)}
              />
              <img 
                src={process.env.PUBLIC_URL + '/b3.png'} 
                alt="Button 3" 
                onClick={() => openLink('https://www.youtube.com/watch?v=GQoPmK8NZAU')}
              />
            </div>
          </div>

            <div className="section">
              <img className="full-width" src={process.env.PUBLIC_URL + '/f1.png'} alt="Image for Section 2" />
            </div>
            <div className="section">
              <img className="full-width" src={process.env.PUBLIC_URL + '/f2.png'} alt="Image for Section 3" />
            </div>
            <div className="section">
              <img className="full-width" src={process.env.PUBLIC_URL + '/f3.png'} alt="Image for Section 4" />
            </div>
            <div className="section">
            <div className="button-container-space-between">
                <img 
                  className="img-button"
                  src={process.env.PUBLIC_URL + '/p1.png'} 
                  alt="Button 1" 
                  onClick={() => openPdf('p1.pdf')}
                />
                <img 
                  className="img-button"
                  src={process.env.PUBLIC_URL + '/p2.png'} 
                  alt="Button 2" 
                  onClick={() => openPdf('p2.pdf')}
                />
              </div>
            </div>

          </ReactFullpage.Wrapper>
        );
      }}
    />
  );
};

ReactDOM.render(
  <React.StrictMode>
    <FullpageComponent />
  </React.StrictMode>,
  document.getElementById('root')
);
